//  name
import hjNews from './src/hj-news'
import mixins from "../../src/mixins/default";

if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.mixin(mixins)
    window.Vue.component(hjNews.name, hjNews)
}

//  install 
hjNews.install = function (Vue) {
    Vue.component(hjNews.name, hjNews)
}

// 
export default hjNews
